import subjects from '../data/subjects';
import courses from '../data/courses';

export default function SubjectGraph({state, dispatch}) {
    if (!state.currentCourse) {
        return null;
    }

    let course = courses[state.currentCourse];
    let allSubjects = course.coreSubjects.concat(course.majorSubjects).concat(course.electiveSubjects);

    function wrapText(text) {
        // this is a naive wrapping algorithm.
        // replace space with <br/> if the number of characters exceeds a threshold
        var counter = 0;
        for (var i = 0; i < text.length; i++) {
            if (text[i] === " " && counter >= 12) {
                text = text.slice(0, i) + "<br/>" + text.slice(i + 1);
                counter = 0;
            } else {
                counter++;
            }
        }
        return text;
    }
    function renderSubjects(list) {
        function selectedStyle(code) {
            return code === state.selectedSubject ? `penwidth="2" color="red"` : "";
        }
        function piggyback(code) {
            if (subjects[code].piggyback) {
                return ` (piggyback: ${subjects[code].piggyback})`;
            } else {
                return "";
            }
        }
        function offerings(code) {
            if (code.includes("Minor")) {
                return "";
            }
            let offered_code = subjects[code].piggyback || code;
            let other_offerings = Object.entries(state.subjectOfferings[offered_code] || {}).filter(([tp, courses]) => !courses.includes(state.currentCourse)).map(([tp, courses]) => tp);
            if (other_offerings.length > 0) {
                return " [also " + other_offerings.join(", ") + "]";
            } else {
                return "";
            }
        }
        return list
            // .filter(code => course.schedule[code] !== undefined) // only render subjects that are in the schedule
            .map(code => `"${code}" [${selectedStyle(code)} id="subject_${code}" label=<<b>${code}</b><br/>${wrapText(subjects[code].name + piggyback(code) + offerings(code))}>]`).join("\n");
    }
    function renderPrereqs(list) {
      return list
        .map(code => subjects[code].prerequisites
            .filter(prereq => allSubjects.includes(prereq))
            .map(prereq => `    "${prereq}" -> "${code}";`))
        .flat()
        .join("\n");
    }

    // check that all named subjects are present in the database 
    for (let code of allSubjects) {
        if (!subjects[code]) {
            return <div>Error: Subject code {code} is named in "{course.name}" but it's not found in the subject database.</div>
        }
    }
  
    // flip around the schedule to organise the subjects by teaching period
    let subjectsByTeachingPeriod = {};
    for (let [code, tp] of Object.entries(state.schedule[state.currentCourse])) {
        if (!subjectsByTeachingPeriod[tp]) {
            subjectsByTeachingPeriod[tp] = [];
        }
        subjectsByTeachingPeriod[tp].push(code);
    }
    
    // generate the graphviz to place the subjects in the correct year levels 
    let subjectPlacement = Object.entries(subjectsByTeachingPeriod).map(([tp, subjects]) => {
        let subjectList = subjects.map(code => `"${code}"`).join(" ");
        return `{ rank=same; "${tp}" ${subjectList} }`;
    }).join("\n");
  
    const graph = `digraph {
        ranksep="0.2 equally";
        ratio="compress";
        size="11!,6";
        node [fontsize=12, shape = plaintext, fontname="Arial"];
        edge [style=invis];
  
        // Set the sequence of study periods
        ${course.teachingPeriods.map(tp => `"${tp}"`).join(" -> ")};
        ${course.teachingPeriods.map(tp => `"${tp}" [label=<${tp.replace(' (block', '<br/>(block')}>]`).join("\n")};
    
    
        edge [style=""];
    
        // Set the visual style for all subjects
        node [
            fontsize=10, 
            shape=box, 
            style="filled,rounded",
            gradientangle=270,
            width=1.2,
            height=0.7
        ]
    
        // Set the visual style for common engineering subjects
        node [
            fillcolor="#FFFFFF:#B4DAF7",
        ]
        ${renderSubjects(course.coreSubjects)}

        // Set the visual style for the engineering major
        node [
            fillcolor="#FFFFFF:#FFB774",
        ]
        ${renderSubjects(course.majorSubjects)}
  
        // Set the visual style for the minor 
        node [
            fillcolor="#FFFFFF:#FFE69C",
        ] 
        ${renderSubjects(course.electiveSubjects)}
  
        // // Set when each subject is taken 
        ${subjectPlacement}
  
        // Set the prerequisites
        ${renderPrereqs(allSubjects)}

    }`;

    // return <pre>{graph}</pre>
    
    const svg = window.viz.renderSVGElement(graph);
    return (<>
      <div width="100%" dangerouslySetInnerHTML={{__html: svg.outerHTML}} 
        onClick={e => {
            // If e.target.parentNode.id begins with "subject_" then set the selectedSubject to the subject code    
            if (e.target.parentNode.id.startsWith("subject_")) {
                let clickedSubject = e.target.parentNode.id.slice(8);
                if (state.selectedSubject === clickedSubject) {
                    dispatch({type: "selectSubject", payload: null});
                } else {
                    dispatch({type: "selectSubject", payload: clickedSubject});
                }
            } else {
                dispatch({type: "selectSubject", payload: null});
            }
        }} 
        />
    </>)
}
