import * as React from 'react';
import { useReducer, useEffect } from 'react';
import './App.css';
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import Form from 'react-bootstrap/Form';

import SubjectOfferings from './components/SubjectOfferings';
import SubjectGraph from './components/SubjectGraph';
import JsonEditor from './components/JsonEditor';
import { initialState } from './state/default';
import { reducer } from './state/reducer';
import SubjectPanel from './components/SubjectPanel';
import courses from './data/courses';

function App() {
  const [state, dispatch] = useReducer(reducer, null, () => reducer(initialState, {type: 'init'}));

  // set up a listener for keyboard events
  useEffect(() => {
    function handleKeyDown(event) {
      if (event.key === "ArrowUp") {
        dispatch({ type: "moveSubject", direction: "up" });
        event.preventDefault();
      } else if (event.key === "ArrowDown") {
        dispatch({ type: "moveSubject", direction: "down" });
        event.preventDefault();
      }
    }
    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, [dispatch]);
  
  return (<>
    <div className='container'>
      <h1>Study planner</h1>
      <p><Button onClick={() => dispatch({type: "reset"})}>Reset study plan to default</Button></p>
      <p>Choose a course:
      <Form.Select onChange={e => dispatch({type: 'setCurrentCourse', payload: e.target.value || null})} value={state.currentCourse || ''}>
        <option value=''></option>
        {
          Object.entries(courses)
          .sort(([_code1, course1], [_code2, course2]) => course1.name.localeCompare(course2.name))
          .map(([code, course]) => (
            <option key={code} value={code}>{course.name}</option>
          ))
        }
      </Form.Select>
      </p>

      <SubjectPanel state={state} dispatch={dispatch} />
      <SubjectGraph state={state} dispatch={dispatch} />

      <h1>Extras</h1>
      <Accordion>
        <Accordion.Item eventKey="0">
          <Accordion.Header>Summary of subject offerings</Accordion.Header>
          <Accordion.Body>
            <SubjectOfferings subjectOfferings={state.subjectOfferings} />
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>

      <Accordion>
        <Accordion.Item eventKey="0">
          <Accordion.Header>Debug info</Accordion.Header>
          <Accordion.Body>
            <textarea rows={30} cols={80} value={JSON.stringify(state.schedule, null)}/>
            <pre>{JSON.stringify(state, null, 2)}</pre>
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>
    </div>
    <p></p>
  </>)
}

export default App;
