import subjects from "../data/subjects";
import courses from "../data/courses";
import { defaultSchedule } from "./default";

function getCourseSubjects(courseCode) {
    let course = courses[courseCode];
    return course.coreSubjects.concat(course.majorSubjects).concat(course.electiveSubjects);
}

function blankSchedule(courseCode, subjectOfferings) {
    // Place subjects in the year level that matches the first digit of their code, and in the study period where there already exists an offering 
    let courseSubjects = getCourseSubjects(courseCode);
    let courseSchedule = {};
    for (let subjectCode of courseSubjects) {
        let year = parseInt(subjectCode[2]);
        let tps = courses[courseCode].teachingPeriods.filter(tp => tp.includes("Year " + year));
        if (tps.length === 0) {
            // nothing matches, just throw it in the first study period
            courseSchedule[subjectCode] = courses[courseCode].teachingPeriods[0];
            continue;
        }
        // find the first teaching period that has an offering
        let offeredTp = tps.find(tp => subjectOfferings[subjectCode]?.[tp]);
        if (offeredTp) {
            courseSchedule[subjectCode] = offeredTp;
        } else {
            courseSchedule[subjectCode] = tps[0];
        }
    }
    return courseSchedule;
}

function scheduleEncode(schedule) {
    // Encode the schedule as a string suitable for a URLSearchParams value. The timing of each
    // subject in the course is represented using a single character, where 'A' means the first
    // teaching period, 'B' means the second teaching period, and so on. Do this for each course.
    let params = new URLSearchParams(window.location.hash.slice(1));
    for (let courseCode in schedule) {
        let courseSchedule = schedule[courseCode];
        let courseScheduleString = "";
        let courseSubjects = getCourseSubjects(courseCode);
        for (let subjectCode of courseSubjects) {
            let tp = courseSchedule[subjectCode];
            let tpIndex = courses[courseCode].teachingPeriods.indexOf(tp) || 0;
            console.log({subjectCode, tp, tpIndex})
            courseScheduleString += String.fromCharCode(65 + tpIndex);
        }
        params.set(courseCode, courseScheduleString);
    }
    // if the URLSearchParams changed from the current browser location, call history.replaceState 
    // to update the URL without causing a page reload
    if (params.toString() !== window.location.hash.slice(1)) {
        window.history.replaceState({}, '', `${window.location.pathname}#${params}`);
    }
}

function scheduleDecode() {
    // Decode the schedule from the URLSearchParams value. This is the reverse of scheduleEncode.
    let params = new URLSearchParams(window.location.hash.slice(1));
    let schedule = {};
    for (let courseCode in courses) {
        let courseSubjects = getCourseSubjects(courseCode);
        let courseScheduleString = params.get(courseCode);
        if (courseScheduleString) {
            let courseSchedule = {};
            for (let i = 0; i < courseScheduleString.length; i++) {
                let subjectCode = courseSubjects[i];
                let tpIndex = courseScheduleString.charCodeAt(i) - 65;
                courseSchedule[subjectCode] = courses[courseCode].teachingPeriods[tpIndex];
            }
            schedule[courseCode] = courseSchedule;
        } else if (defaultSchedule[courseCode]) {
            schedule[courseCode] = defaultSchedule[courseCode];
        } else {
            // no default schedule, just make something up
            schedule[courseCode] = blankSchedule(courseCode, {});
        }
    }
    return schedule;
}

function calcSubjectOfferings(schedule) {
    let subjectOfferings = {};
    for (let subjectCode in subjects) {
        // piggyback subjects are listed under the main subject code
        let offeredCode = subjects[subjectCode].piggyback || subjectCode;
        for (let tp of ["TR1", "TR2", "TR3"]) {
            for (let courseCode of Object.keys(courses)) {
                if (schedule[courseCode][subjectCode]?.includes(tp)) {
                    subjectOfferings[offeredCode] = subjectOfferings[offeredCode] || {};
                    subjectOfferings[offeredCode][tp] = subjectOfferings[offeredCode][tp] || [];
                    subjectOfferings[offeredCode][tp].push(courseCode);
                }
            }
        }
    }
    return subjectOfferings;
}

// Initial step of the reducer (not running the middleware)
export function reducer(state, action) {
    // Decode the schedule from the URL or load the default 
    let schedule = scheduleDecode();
    let newState = { ...state, schedule };
    switch (action.type) {
        case "init":
            // called when the app is initialised to fill in the calculated parts of the state 
            break;
        case "reset":
            // reset the study plan to the default 
            for (let courseCode in courses) {
                if (defaultSchedule[courseCode]) {
                    newState.schedule[courseCode] = defaultSchedule[courseCode];
                } else {
                    // no default schedule, just make something up
                    newState.schedule[courseCode] = blankSchedule(courseCode, {});
                }
            }
            break;
        case "setCurrentCourse":
            newState.currentCourse = action.payload;
            newState.selectedSubject = null;
            break;
        case "selectSubject":
            newState.selectedSubject = action.payload;
            break;
        case "moveSubject":
            if (state.selectedSubject === null) {
                break;
            }
            let currentStudyPeriod = state.schedule[state.currentCourse][state.selectedSubject];
            let newStudyPeriod;
            if (!currentStudyPeriod) {
                // if the subject was not in the schedule, move it to the first study period
                newStudyPeriod = courses[state.currentCourse].teachingPeriods[0];
            } else {
                let index = courses[state.currentCourse].teachingPeriods.indexOf(currentStudyPeriod);
                if (action.direction === "up") {
                    if (index === 0) {
                        break;
                    }
                    newStudyPeriod = courses[state.currentCourse].teachingPeriods[index - 1];
                } else {
                    if (index === courses[state.currentCourse].teachingPeriods.length - 1) {
                        break;
                    }
                    newStudyPeriod = courses[state.currentCourse].teachingPeriods[index + 1];
                }
            }
            newState.schedule[state.currentCourse][state.selectedSubject] = newStudyPeriod;
            break;
        default: {
            throw Error('Unknown action: ' + action.type);
        }
    }

    // Calculate the subject offerings
    newState.subjectOfferings = calcSubjectOfferings(newState.schedule);

    // Encode the schedule to the URL
    scheduleEncode(newState.schedule);

    // Done
    return newState;
}
