export function sortSubjects(array) {
    // Sort subjects by the number first then the letter code
    return array.sort((a, b) => {
        let num_a = parseInt(a.slice(2));
        let num_b = parseInt(b.slice(2));
        let letter_a = a.slice(0, 2);
        let letter_b = b.slice(0, 2);

        if (num_a < num_b) {
            return -1;
        } else if (num_a > num_b) {
            return 1;
        } else {
            if (letter_a < letter_b) {
                return -1;
            } else if (letter_a > letter_b) {
                return 1;
            } else {
                return 0;
            }
        }
    });   
}
