import { sortSubjects } from '../util/util';
import subjects from '../data/subjects';
import courses from '../data/courses';

export default function SubjectOfferings({subjectOfferings}) {
    let codes = sortSubjects(Object.keys(subjects));
    // filter out subjects that are not in the offerings
    codes = codes.filter(code => subjectOfferings[code]);

    return (
        <table className='table table-striped'>
            <thead>
                <tr>
                    <th scope='col'>Code</th>
                    <th scope='col'>Name</th>
                    <th scope='col'>TR1</th>
                    <th scope='col'>TR2</th>
                    <th scope='col'>TR3</th>
                </tr>
            </thead>
            <tbody>
                { 
                    codes.map(code => (
                        <tr key={code}>
                            <td>{code}</td>
                            <td>{subjects[code].name}</td>
                            {["TR1", "TR2", "TR3"].map(tp => (
                                <td key={tp}>
                                    <ul>
                                        {subjectOfferings[code][tp]?.map(courseCode => (
                                            <li key={courseCode}>{courses[courseCode].shortName}</li>
                                        ))}
                                    </ul>
                                </td>
                            ))}
                        </tr>
                    ))
                }
            </tbody>
        </table>
    )
}
