import Button from 'react-bootstrap/Button';
import subjects from '../data/subjects';

export default function SubjectPanel({state, dispatch}) {
    if (state.selectedSubject) {
        let subject = subjects[state.selectedSubject];
        let prerequisites = subject.prerequisites.map(code => code + " " + subjects[code].name).join(", ");
        return <div className="subject-panel">
            <h4>Selected subject: {state.selectedSubject} {subject.name}</h4>
            { prerequisites ? <p>Prerequisites: {prerequisites}</p> : null }
            <p>Use arrow keys on keyboard or press these buttons to move the selected subject: {' '}
            <Button variant="outline-dark" onClick={() => dispatch({type: "moveSubject", direction: "up"})}>Move up</Button>
            <Button variant="outline-dark" onClick={() => dispatch({type: "moveSubject", direction: "down"})}>Move down</Button>
            </p>
        </div>
    } else {
        return <div className="subject-panel">
            <p>Click on a subject to select it, then use the up/down arrow keys to move it.</p>
        </div>
    }
}
